@import "./theme.scss";

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  margin: 0;
  background: $bodyBackground;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.full-width {
  width: 100%;
  max-width: 100%;
}

.interior-container {
  width: 100%;
  max-width: $maxWidth;
  margin: 0 auto;
}

.inputs-container {
  width: 100%;
  min-height: 500px;
  margin-top: 10px;
  &-top {
    background: $offsetBGColor;
    color: $offsetFontColor;
    padding: 5px;
    font-weight: 500;
  }
}

.header {
  color: $primaryColor;
  background: $headerColor;
  height: 60px;
  display: flex;
  align-items: center;
  border-top: 5px solid $secondaryColor;
}

.header-title {
  font-size: $headerTitleSize;
  font-family: $headerFont;
  font-weight: 600;
  padding-left: 5px;
  color: $headerFontColor;
}

.header-logo {
  width: 100%;
  margin-top: 20px;
  background: url("../images/logo1.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.logo-img {
  width: 150px;
  margin-top: 20px;
}

.flex-row {
  display: flex;
  flex-direction: row;
  margin: 5px 0;

  .text-input {
    max-width: 160px;
    height: 25px;
    border-bottom: 1px solid #444;
    border-top: none;
    border-right: none;
    border-left: none;
    font-size: 20px;
    padding: 8px;
    background: $secondaryColor;
    color: #fff;
    text-align: right;

    &:read-only {
      border-bottom: none;
      background: transparent;
      color: #444;
    }
  }
}

.flex-middle {
  align-items: center;
}

.flex-col-20 {
  width: 20%;
}

.flex-col-30 {
  width: 30%;
}

.flex-col-40 {
  width: 40%;
}

.flex-col-50 {
  width: 50%;
}

.flex-col-60 {
  width: 60%;
}

.flex-col-70 {
  width: 70%;
}

.inputs-rows-container {
  &:last-child {
    margin-bottom: 100px;
  }
}

.inputs-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  background: $calculatorRowBackground;
  margin-top: 10px;
  position: relative;
  // -moz-transition: all 0.2s ease-in;
  // -o-transition: all 0.2s ease-in;
  // -webkit-transition: all 0.2s ease-in;
  border-left: 10px solid $calculatorRowBackground;
  border-left: 5px solid $secondaryColor;

  &-title {
    font-weight: 600;
    color: $calculatorRowTitleColor;
  }
  &:hover {
    background: $calculatorRowHoverColor;
    border-left: 5px solid $secondaryColor;
  }
  &-body {
    padding: 25px;
    width: 100%;
  }
}

.caret-container {
  height: 20px;
  width: 20px;
  position: absolute;
  top: calc(50% - 10px);
  left: 0px;
}

.caret {
  position: relative;
}

.caret:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  border-left: 10px solid $secondaryColor;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
}

.caret:after {
  content: "";
  position: absolute;
  left: 0;
  top: 1px;
  border-left: 9px solid $secondaryColor;
  border-top: 9px solid transparent;
  border-bottom: 9px solid transparent;
}

.fade-in {
  opacity: 0;
}
.fade-in.visible {
  transition: opacity 3s ease-in;
  opacity: 1;
}

/* CALCULATOR AREA */
.calculator-body {
  position: fixed;
  display: flex;
  align-items: center;
  bottom: 0;
  background: #fff;
  width: 100%;
  max-width: 1200px;

  .calculator-container {
    width: 100%;

    .wrapper {
      margin-bottom: 20px;
      flex-grow: 1;
      background: $footerColor;
      text-align: center;

      .calculator-title {
        font-weight: 600;
        color: $footerFontColor;
        font-size: 20px;
      }

      .calculator-value {
        font-weight: 700;
        font-size: 36px;
      }

      &.cost {
        .calculator-value {
          //color: #eb5757;
          color: $footerFontColor;
        }
      }

      &.productivity {
        .calculator-value {
          //color: #8bc53f;
          color: $footerFontColor;
        }
      }

      &.net {
        .calculator-value {
          //color: #4a7bad;
          color: $calcHighlightColor;
        }
      }

      &.roi {
        .calculator-value {
          //color: #ff7348;
          color: $footerFontColor;
        }
      }
    }
  }
}

.calculator-container-top {
  text-align: center;
}

.inputs-row-title {
  font-size: 20px;
  margin-bottom: 10px;
}
